import "./appBanner.scss";
import avengers from "../../resources/img/Avengers.png";
import avengersLogo from "../../resources/img/Avengers_logo.png";

const AppBanner = () => {
    return (
        <div className="app__banner">
            <img src={avengers} alt="Avengers" className="app__banner-img"/>
            <h1 className="app__banner-text">Avengers</h1>
            <img src={avengersLogo} alt="Avengers logo" className="app__banner-img"/>
        </div>
    )
}

export default AppBanner